@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap');

:root {
  --footerBackground: rgb(148, 0, 211);
}

* {
  font-family: 'Roboto', sans-serif;
}

.footer {
  width: 100%;
  height: 200px;
  background-color: #4158d0;
  background-image: linear-gradient(
    90deg,
    rgba(255, 206, 23, 1) 0%,
    rgba(255, 100, 100, 1) 50%
  );
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 10px;
  margin-top: 0px;
}

.socialMedia svg {
  color: white;
  margin: 1rem;
  font-size: 2rem;
  cursor: pointer;
}

.socialMedia svg:hover {
  color: rgba(57, 0, 86, 1);
}

.footer p {
  color: white;
  font-size: 1.2rem;
  font-weight: 300;
}

@media only screen and (max-width: 600px) {
  .footer svg {
    font-size: 2rem;
  }
}
