@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap');

:root {
  --text: #fff;
}

* {
  font-family: 'Roboto', sans-serif;
  font-size: 400;
}

.navbar {
  width: 100%;
  height: 100px;
  background-color: #4158d0;
  background-image: linear-gradient(
    90deg,
    rgba(255, 206, 23, 1) 0%,
    rgba(255, 100, 100, 1) 50%
  );
}

.links {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.navbar a {
  color: white;
  text-decoration: none;
  margin: 2rem;
  font-size: 1.6rem;
}

.navbar a:hover {
  background: linear-gradient(
    90deg,
    rgba(57, 0, 86, 1) 0%,
    rgba(78, 0, 71, 1) 100%
  );
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.toggleButton {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.toggleButton svg {
  font-size: 50px;
}
.toggleButton button {
  margin-right: 20px;
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
}

#open {
  height: 100vh;
}

#open .links {
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 100px;
}

#open a {
  width: 100%;
  text-align: center;
}

@media only screen and (max-width: 900px) {
  .navbar a {
    width: 80vw;
  }
}

@media only screen and (max-width: 600px) {
  .toggleButton {
    display: flex;
  }
  #close a {
    display: none;
  }
}

@media only screen and (min-width: 601px) {
  .toggleButton {
    display: none;
  }

  .hiddenLinks {
    display: none;
  }

  #open {
    height: 100px;
  }

  #open .links {
    flex-direction: row;
  }
}
